import Bootstrap from '../../../components/Bootstrap'
import NavBar from '../../../components/NavBar'
import PrimaryContainer from '../../../components/PrimaryContainer'

import React from 'react'
import 'isomorphic-unfetch'

export default class VoIPViewer extends React.Component {
  async componentDidMount () {
    // eslint-disable-next-line no-undef
    const res = await fetch('https://api.github.com/repos/developit/preact')
    const json = await res.json()
    console.log("boo")
      this.data = { stars: json.stargazers_count }
      this.setState()
  }

    render () {
    var dataPreview;
    if (this.data) {
      dataPreview = <p>Preact has {this.data.stars} ⭐️</p>
    } else {
      dataPreview = <div>Loading...</div>
    }
    return (
     <div>
    <Bootstrap>
      <title>Packet Loss Gaming - VoIP Current Users</title>
    </Bootstrap>
    <NavBar></NavBar>
    <PrimaryContainer>
      <div>
            {dataPreview}
      </div>
            </PrimaryContainer>
            </div>
    )
  }
}

